import React from "react";
import { Layout } from "../components/Layout";
import { IHeaderProps } from '../components/Header';
import { FaCogs } from "react-icons/fa";
import { constants } from "../constants";

export class ApiPage extends React.Component
{
  render()
  {
    let header: IHeaderProps = {
      url: "/",
      type: "website",
      title: "M Formatter | Beautify your PowerQuery code",
      description: "Report a bug for M Formatter"
    };
    return (
      <Layout header={header}>
        <h1><FaCogs style={{position: "relative", top: ".15em"}} /> Api Documentation</h1>
        <p>
          The formatting service is hosted as an azure function. To use the api you need to send a <b>POST</b> request to 
          <b> {constants.formatterApiUrl} </b> 
          with content type <b>application/json</b> and the following body structure:
          <div dangerouslySetInnerHTML={{__html: `<pre class="hljs" style="display: block; overflow-x: auto; padding: 0.5em; background: rgb(255, 255, 255); color: rgb(0, 0, 0);"><span class="hljs-keyword" style="color: rgb(0, 0, 255);">type</span> RequestBody =
{
     code: <span class="hljs-built_in" style="color: rgb(0, 0, 255);">string</span>,
     config: IConfig
}</pre>`}}>
          </div>
          A usage example is provided in <a href="https://github.com/UliPlabst/powerquery-formatter-examples">our example repository.</a><br/>
        <p>
          The config object is defined by
          <div dangerouslySetInnerHTML={{__html: `<pre class="hljs" style="display: block; overflow-x: auto; padding: 0.5em; background: rgb(255, 255, 255); color: rgb(0, 0, 0);"><span class="hljs-keyword" style="color: rgb(0, 0, 255);">export</span> <span class="hljs-keyword" style="color: rgb(0, 0, 255);">interface</span> IConfig
{
    <span class="hljs-comment" style="color: green;">//String that is used for indentation, default = "&amp;nbsp;&amp;nbsp;"</span>
    indentation?: <span class="hljs-built_in" style="color: rgb(0, 0, 255);">string</span>;
    <span class="hljs-comment" style="color: green;">//Length of the indentation string (is used for determining when the max line width is reached)</span>
    indentationLength?: <span class="hljs-built_in" style="color: rgb(0, 0, 255);">number</span>;
    <span class="hljs-comment" style="color: green;">//String that is used to terminate a line, default = "&lt;br/&gt;"</span>
    lineEnd?: <span class="hljs-built_in" style="color: rgb(0, 0, 255);">string</span>;
    <span class="hljs-comment" style="color: green;">//String that is used as a whitespace, default = "&amp;nbsp;"</span>
    ws?: <span class="hljs-built_in" style="color: rgb(0, 0, 255);">string</span>;
    <span class="hljs-comment" style="color: green;">//Maximum line width, default = 100. The formatter forces expression to break into several lines if the maximum line width would be reached. Please not some expressions cannot be broken into multiple lines such as very long identifiers and therefore the maximum line width can be exceeded in these cases. As of now there is no warning to give you info about that.</span>
    lineWidth?: <span class="hljs-built_in" style="color: rgb(0, 0, 255);">number</span>;
    <span class="hljs-comment" style="color: green;">//Number of different bracket classes, default = 3. The formatter assigns bracket pairs the classes 'bracket' and 'bracket-{num}' where num = 1...{numBrackets}. You can then style your matching brackets with configurable depth.</span>
    numBrackets?: <span class="hljs-built_in" style="color: rgb(0, 0, 255);">number</span>;
    <span class="hljs-comment" style="color: green;">//All text (code) is escaped into html format, default = true.</span>
    escapeHtmlText?: <span class="hljs-built_in" style="color: rgb(0, 0, 255);">boolean</span>;
    <span class="hljs-comment" style="color: green;">//Align paired expressions by their equal sign, default = false.</span>
    alignPairedExpressionsByEqual?: <span class="hljs-built_in" style="color: rgb(0, 0, 255);">boolean</span>;
    <span class="hljs-comment" style="color: green;">//If null the formatter will put classes onto the resulting html but not styles (can be used for websites where you want to style the formatted html with css yourself). If non null the classes will be translated to inline styles on the html elements. See below for more information about the used classes.</span>
    inlineCss?: <span class="hljs-built_in" style="color: rgb(0, 0, 255);">string</span>;
}</pre>`}}>
          </div>
          </p>
          <p>
          The css classes assigned by the formatter that can be used to customize the style either via the <b>inlineCss</b> config option or via css on your webpage are 
          <ul>
            <li>constant
            <ul>
              <li>keyword</li>
              <li>identifier</li>
              <ul>
                <li>method-call</li>
              </ul>
              <li>operator</li>
              <li>bracket</li>
              <li>bracket-{'{'}num{'}'}</li>
              <li>type</li>
            </ul>
              </li>
              <li>literal
              <ul>
              <li>string</li>
              <li>list</li>
              <li>boolean</li>
              <li>number</li>
              <li>null</li>
              <li>record</li>
              <li>string</li>
            </ul>
            </li>
          </ul>
          </p>
        </p>
        <p>
          <h3>Terms of use</h3>
          You are free to use the api as long as you give credit and (if you use the formatter for your website) link back to this site.
        </p>
      </Layout>
    );
  }
}

export default ApiPage;